/*Desktop View*/
.header {
  position: fixed;
  /* top:0; */
  width: 100vw;
  display: flex;
  background-color: rgb(0, 0, 0, 0.96);
  /* border-bottom: 1px solid rgb(243, 243, 243); */
  justify-content: center;
  align-items: center;
  /* padding: 0px 40px; */
  height: 60px;
  z-index: 99;
  box-sizing: border-box;
  transition: opacity 500ms ease-in-out;
}
.header2 {
  position: fixed;
  width: 100vw;
  display: flex;
  background-color: rgb(0, 0, 0, 0.96);
  justify-content: center;
  align-items: center;
  height: 60px;
  z-index: 99;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 300ms ease-out;
}
.logo-nav {
  padding: 0;
  position: absolute;
  left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  height: 45px;
}
.nav-options {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
}
.mobile-option {
  display: none;
}
.option {
  padding: 10px 20px;
  text-decoration: none;
}
.option:hover {
  cursor: pointer;
}
.link {
  text-decoration: none;
  font-size: 18px;
  color: #59d2fe;
  transition: 0.2s ease;
}

.link:hover {
  color: #ffffff;
}

.mobile-menu {
  display: none;
}
@media (max-width: 768px) {
  /*Mobile View */
  .header {
   
    background-color: rgb(0, 0, 0, 0.0);
  
  }
  .header2 {
  
    background-color: rgb(0, 0, 0, 0.0);
   
  }

  .logo {
    width: 45px;
    height: 45px;
  }
  .nav-options {
    display: flex;
    width: 100vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: absolute;
    left: 00px;
    top: 00px;
    left: -500px;
    right: 0;
    opacity: 0;
    transition: all 0.5s ease;
    flex-direction: column;
    list-style-type: none;
    padding: 10px 0;
  }
  .nav-options.active {
    background-color: rgb(0, 0, 0, 0.96);
    left: 0px;
    /* right: 0; */
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
  }

  .menu-icon {
    position: absolute;
    top: -22px;
    right: -5px;
    width: 30px;
    height: 45px;
    color: #59d2fe;
  }
  .option {
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 10vw; */
    padding: 20px 0px;
    width: 100%;
    /* border-bottom: 1px solid rgb(243, 243, 243); */
  }
}
.mobile-menu {
  position: absolute;
  display: block;
  right: 20px;
  z-index: 999;
}

