  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* box-shadow: 1px 1px 6px rgb(163, 163, 163); */

  /* margin:1px */
}

.project-image {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: red; */
}
.project-description {
  position: relative;
  z-index: 2;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity 200ms ease-in-out;
}
.project-description > h3 {
  padding: 0 16px;
