  padding: 10px;
  border-radius: 20px;
  position: absolute;
  bottom: 30px;
  right: 30px;
}
.site-link:hover {
  transform: scale(1.05);
}
.project:hover .project-description {
  opacity: 1;
  background-color: rgba(163, 231, 255, 0.85);
}
img {
  width: 100%;
  height: 100%;
  /* object-fit: contain; */
}
